import React, { useState, useEffect } from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  InputGroup,
  Input,
  Card,
  CardBody,
  FormGroup,
  Form,
  Row,
  Col,
  Label,
} from "reactstrap";
import Select from "react-select";
import { useDispatch, useSelector } from "react-redux";
import { postXaridData } from "../../../../redux/action/xarid/uzAction";
import { getKontData } from "../../../../redux/action/kassa/kontAction";
import { getAcData } from "../../../../redux/action/kassa/acAction";
import { getFuraData } from "redux/action/xarid/furaAction";
import { getProoductData } from "redux/action/xarid/productName";
import { TextField } from "@material-ui/core";
import "./styles.css";
const createOption = (label, value) => ({
  label,
  value,
});
const ModalExample = (props) => {
  const dispatch = useDispatch();
  const { kont } = useSelector((state) => state.kont);
  const { ac } = useSelector((state) => state.account);
  const { fura } = useSelector((state) => state.fura);
  const { product } = useSelector((state) => state.product);
  const defaultOption2 = kont.map((e) => createOption(e.name, e.id));
  const [konts, setKonts] = useState({
    isLoading: false,
    options: [...defaultOption2],
    value: undefined,
  });
  const { value } = konts;
  const defaultOption = ac.map((e) => createOption(e.name, e.id));
  const [acc, setAcc] = useState({
    options1: [...defaultOption],
    value1: undefined,
  });
  const { value1 } = acc;
  const defaultOption1 = fura.map((e) => createOption(e.name, e.id));
  const [furaa, setFura] = useState({
    options2: [...defaultOption1],
    value2: undefined,
  });
  const { value2 } = furaa;
  const defaultOption3 = product.map((e) => createOption(e.name, e.id));
  const [products, setProduct] = useState({
    options3: [...defaultOption1],
    value3: undefined,
  });
  const { value3 } = products;

  useEffect(() => {
    const getKont = async () => {
      dispatch(getKontData());
      dispatch(getAcData());
      dispatch(getFuraData());
      dispatch(getProoductData());
    };
    getKont();
  }, []);

  const [purNumber, setPurNumber] = useState("");
  const [date, setDate] = useState("");
  const [oxNumber, setOx] = useState(null);
  const [loss, setLoss] = useState(null);
  const [amount, setAmount] = useState(null);
  const [price, setPrice] = useState(null);
  const [izoh, setIzoh] = useState("");
  const [date1, setDate1] = useState("");
  const [modal, setModal] = useState(false);
  const toggle = () => setModal(!modal);
  const postXarid = () => {
    const dataXarid = {
      salesNumber: purNumber,
      data: date,
      country: 1,
      productname: value3?.value,
      slaughteredCattle: oxNumber,
      amount: amount,
      loss: loss,
      price: price,
      nds: 0,
      veterinar: 0,
      goStandart: 0,
      gigiena: 0,
      customer: 0,
      deklarent: 0,
      delivery: 0,
      swift: 0,
      ministryFinance: 0,
      certificate: 0,
      sPOT: 0,
      sES: 0,
      accountnumber: value1?.value,
      furaCondition: value2?.value,
      finisheddate: date1,
      willBePaid: 0,
      residue: 5,
      buymonth: new Date(date).getMonth(),
      finishedmonth: new Date(date1).getMonth(),
      comment: izoh,
    };
    console.log(
      "ffffff",
      new Date(date).getMonth(),
      new Date(date1).getMonth(),
      value3?.value
    );
    dispatch(postXaridData(dataXarid));
    setPurNumber("");
    setDate("");
    setOx("");
    setLoss("");
    setAmount("");
    setPrice("");
    setIzoh("");
    setDate1("");
    setProduct({ value3: "" });
    setFura({ value2: "" });
    setKonts({ value: "" });
    setAcc({ value1: "" });
  };

  return (
    <div>
      <Button color="danger" onClick={toggle} style={{ width: "100%" }}>
        Ўзбек
      </Button>
      <Modal
        isOpen={modal}
        toggle={toggle}
        size="lg"
        style={{ maxWidth: "1600px", width: "80%", margin: "10px auto" }}
      >
        <ModalHeader toggle={toggle}>Ўзбек</ModalHeader>
        <ModalBody>
          <Card
            className="shadow border-0"
            style={{ backgroundColor: "#E5F0FF" }}
          >
            <CardBody className="px-lg-5 py-lg-3">
              <Form role="form" name="google-sheet" method="POST">
                <Row>
                  <Col lg="6" xl="6">
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Харид номери"
                          type="text"
                          value={purNumber}
                          onChange={(e) => setPurNumber(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Сана"
                          type="date"
                          value={date}
                          onChange={(e) => setDate(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <Select
                        placeholder={"Контрагент"}
                        isClearable
                        onChange={(e) => setKonts({ value: e })}
                        options={defaultOption2}
                        value={value}
                      />
                    </FormGroup>
                    <FormGroup>
                      <Select
                        placeholder={"Маҳсулот номи"}
                        isClearable
                        value={value3}
                        options={defaultOption3}
                        onChange={(e) => setProduct({ value3: e })}
                      />
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Сўйилган моллар сони"
                          type="number"
                          value={oxNumber}
                          onChange={(e) => setOx(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <Select
                        placeholder={"Фура"}
                        value={value2}
                        isClearable
                        options={defaultOption1}
                        onChange={(e) => setFura({ value2: e })}
                      />
                    </FormGroup>
                  </Col>
                  <Col lg="6" xl="6">
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Миқдори"
                          type="number"
                          value={amount}
                          onChange={(e) => setAmount(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Гўшт йўқотилиши"
                          type="number"
                          value={loss}
                          onChange={(e) => setLoss(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Нарх"
                          type="number"
                          value={price}
                          onChange={(e) => setPrice(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                    <FormGroup>
                      <Select
                        placeholder={"Тўлов тури"}
                        isClearable
                        options={defaultOption}
                        value={value1}
                        onChange={(e) => setAcc({ value1: e })}
                      />
                    </FormGroup>

                    <FormGroup>
                      <InputGroup className="input-group-alternative mb-3">
                        <Input
                          placeholder="Тўлов санаси"
                          type="date"
                          value={date1}
                          onChange={(e) => setDate1(e.target.value)}
                        />
                      </InputGroup>
                    </FormGroup>
                  </Col>
                </Row>
                <FormGroup>
                  <textarea
                    type="text"
                    className="form-control"
                    placeholder="Izoh"
                    value={izoh}
                    onChange={(e) => setIzoh(e.target.value)}
                  />
                </FormGroup>
                <div className="text-right">
                  <Button className="mt-4" color="primary" onClick={postXarid}>
                    Jadvalga qo'shish
                  </Button>
                </div>
              </Form>
            </CardBody>
          </Card>
        </ModalBody>
        <ModalFooter></ModalFooter>
      </Modal>
    </div>
  );
};

export default ModalExample;
