import { useEffect, useState } from "react";
import Tabletop from "tabletop";
function Data() {
  const [data, setData] = useState([]);
  useEffect(() => {
    const getData = async () => {
      await Tabletop.init({
        key: "1InQEqc8Nyhy1jG2vjfHVQFuKGt7WamXJ7wO7DqVggLs",
        simpleSheet: true,
      })
        .then((data) => setData(data))
        .catch((err) => console.warn(err));
    };
    getData();
  }, []);

  //   let datas = data[0];
  return data;
}

export default Data;
