import React, { useState, useEffect } from "react";
import Tabletop from "tabletop";
// reactstrap components
import { Card, CardBody, CardTitle, Container, Row, Col } from "reactstrap";

const Header = () => {
  const [data, setData] = useState([]);
  useEffect(() => {
    const getData = async () => {
      await Tabletop.init({
        key: "1x7j2ab6iQHxmAv2Pt5PzggUvKvH_GnCe9-MvcR6GFMo",
        simpleSheet: true,
      })
        .then((data) => setData(data))
        .catch((err) => console.warn(err));
    };
    getData();
  }, []);
  console.log("Dataaaa", data[0]);
  let datas = data[0];
  const dates = new Date();

  return (
    <>
      <div className="header  pb-6 mt-3 ">
        <Container fluid>
          <div className="header-body ">
            <Row style={{ marginBottom: "1rem" }}>
              <Col lg={9} xl={9} sm={12}>
                <Card className="card-stats mb-4 mb-xl-0 text-center p-2">
                  <h2>Улушдорлар учун дашборд </h2>
                </Card>
              </Col>
              <Col lg="3" xl="3" sm={4}>
                <Card className="card-stats mb-4 mb-xl-0">
                  <CardBody>
                    <Row>
                      <div className="col">
                        <CardTitle
                          tag="h2"
                          style={{ position: "relative" }}
                          className="text-uppercase text-muted mb-0"
                        >
                          <span
                            style={{
                              position: "absolute",
                              top: "-10px",
                              fontSize: "10px",
                            }}
                          >
                            Sana
                          </span>
                          {dates.toLocaleDateString()}
                        </CardTitle>
                        <span className="h2 font-weight-bold mb-0"></span>
                      </div>
                      <Col
                        tag="h2"
                        style={{ position: "relative" }}
                        className=" col-auto text-uppercase text-muted mb-0"
                      >
                        {/* <div className="icon icon-shape bg-danger text-white rounded-circle shadow">
                          <i class="fas fa-money-bill-alt"></i>
                        </div> */}
                        <span
                          style={{
                            position: "absolute",
                            top: "-10px",
                            fontSize: "10px",
                          }}
                        >
                          Oy
                        </span>
                        {dates.getMonth() + 1}
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            </Row>
            {/* Card stats */}
          </div>
        </Container>
      </div>
    </>
  );
};

export default Header;
