import React from "react";
import { Link } from "react-router-dom";
// reactstrap components
import {
  UncontrolledDropdown,
  DropdownToggle,
  Navbar,
  Nav,
  Container,
  Media,
} from "reactstrap";
import { Avatar } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../../redux/action/userAction";
import { BiExit } from "react-icons/bi";

const AdminNavbar = (props) => {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.userLogin);
  const { userInfo } = user;
  const logoutHandler = () => {
    dispatch(logout());
    console.log("userInfooo---------------------------", userInfo);
  };
  return (
    <>
      <Navbar className="navbar-top navbar-dark " expand="md" id="navbar-main">
        <Container fluid>
          <h4 className="h4 mb-0 text-white text-uppercase  d-inline-block">
            {props.brandText}
          </h4>

          <Nav className="align-items-center  d-flex" navbar>
            <UncontrolledDropdown nav>
              <DropdownToggle className="pr-0 hov" nav>
                <Media className="align-items-center">
                  <Media className="mr-2 d-none d-lg-block">
                    <span className="mb-0 text-sm font-weight-bold">Admin</span>
                  </Media>
                  <Avatar className="bg-primary">H</Avatar>
                  <BiExit
                    className="hov-icon"
                    style={{ marginLeft: 10, fontSize: "26px" }}
                    onClick={logoutHandler}
                  />
                </Media>
              </DropdownToggle>
            </UncontrolledDropdown>
          </Nav>
        </Container>
      </Navbar>
    </>
  );
};

export default AdminNavbar;
