import React, { useState } from "react";
// node.js library that concatenates classes (strings)
import classnames from "classnames";
// javascipt plugin for creating charts

import Chart from "chart.js";

// react plugin used to create charts

import AreaCharts from "charts/YalpiChart";
import HeaderYalpi from "components/Headers/HeaderYalpi";
// reactstrap components
import { Container, Row, Col } from "reactstrap";

// core components
import { chartOptions, parseOptions } from "variables/charts.js";
import YalpiCard from "./CardYalpi";

const Index = (props) => {
  if (window.Chart) {
    parseOptions(Chart, chartOptions());
  }

  return (
    <>
      {/* Page content */}
      <HeaderYalpi />
      <Container className="mt--7 pb-5" fluid>
        <Row style={{ marginTop: "2rem" }}>
          <Col className="mb-5 mb-xl-0" xl="3" lg={3}>
            <YalpiCard />
          </Col>
          <Col className="mb-5 mb-xl-0" xl="9" lg={9}>
            <AreaCharts />
          </Col>
        </Row>
      </Container>

      {/* <Pie/> */}
    </>
  );
};

export default Index;
