import React, { useState, useEffect } from "react";
import Tabletop from "tabletop";
// reactstrap components
import { Card, CardBody, CardTitle, Container, Row, Col } from "reactstrap";

const Header = () => {
  const [data, setData] = useState([]);
  useEffect(() => {
    const getData = async () => {
      await Tabletop.init({
        key: "1x7j2ab6iQHxmAv2Pt5PzggUvKvH_GnCe9-MvcR6GFMo",
        simpleSheet: true,
      })
        .then((data) => setData(data))
        .catch((err) => console.warn(err));
    };
    getData();
  }, []);
  console.log("Dataaaa", data[0]);
  let datas = data[0];

  return (
    <>
      <div className="">
        <div className="header-body ">
          {/* Card stats */}
          <Row>
            <Col lg="12" xl="12" className="mb-3">
              <Card className="card-stats mb-4 mb-xl-0">
                <CardBody>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      marginTop: ".5rem",
                    }}
                  >
                    <h3
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        color: "gray",
                        flexDirection: "column",
                        textAlign: "center",
                      }}
                    >
                      <span
                        style={{
                          fontWeight: "bold",
                          textAlign: "left",
                          fontSize: ".8rem",
                        }}
                      >
                        Айланма маблағлар{" "}
                      </span>{" "}
                      <span
                        style={{
                          fontWeight: "bold",
                          color: "#171717",
                          border: "2px solid #12AEF0",
                          marginTop: ".5rem",
                          borderRadius: "5px",
                          paddingTop: ".3rem",
                          paddingBottom: ".3rem",
                        }}
                      >
                        {datas?.ay}
                      </span>
                    </h3>
                  </div>
                </CardBody>
              </Card>
            </Col>
            <Col lg="12" xl="12" className="mb-3">
              <Card className="card-stats mb-4 mb-xl-0">
                <CardBody>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      marginTop: ".5rem",
                    }}
                  >
                    <h3
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        color: "gray",
                        flexDirection: "column",
                        textAlign: "center",
                      }}
                    >
                      <span
                        style={{
                          fontWeight: "bold",
                          textAlign: "left",
                          fontSize: ".8rem",
                        }}
                      >
                        Мажбуриятлар{" "}
                      </span>{" "}
                      <span
                        style={{
                          fontWeight: "bold",
                          color: "#171717",
                          border: "2px solid #12AEF0",
                          marginTop: "0.1rem",
                          borderRadius: "5px",
                          paddingTop: ".3rem",
                          paddingBottom: ".3rem",
                        }}
                      >
                        {datas?.maj}
                      </span>
                    </h3>
                  </div>
                </CardBody>
              </Card>
            </Col>
            <Col lg="12" xl="12" className="mb-sm-0 mb-xs-0">
              <Card className="card-stats mb-4 mb-xl-0">
                <CardBody>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      marginTop: ".5rem",
                    }}
                  >
                    <h3
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        color: "gray",
                        flexDirection: "column",
                        textAlign: "center",
                      }}
                    >
                      <span
                        style={{
                          fontWeight: "bold",
                          textAlign: "left",
                          fontSize: ".8rem",
                        }}
                      >
                        Фарқ{" "}
                      </span>{" "}
                      <span
                        style={{
                          fontWeight: "bold",
                          color: "#171717",
                          border: "2px solid #12AEF0",
                          marginTop: ".5rem",
                          borderRadius: "5px",
                          paddingTop: ".3rem",
                          paddingBottom: ".3rem",
                        }}
                      >
                        {datas?.ulFarq}
                      </span>
                    </h3>
                  </div>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </>
  );
};

export default Header;
