import React from "react";
const Row = ({ row }) => {
  const {
    id,
    salesNumber,
    data,
    country,

    slaughteredCattle,
    amount,
    loss,
    price,
    purchase,
    nds,
    veterinar,
    goStandart,
    gigiena,
    customer,
    deklarent,
    delivery,
    swift,
    thatsall,
    accountnumber,
    finisheddate,
    residue,
    buymonth,
    finishedmonth,
    comment,
    ministryFinance,
    certificate,
    sPOT,
    sES,
    furaCondition,
    willBePaid,
    productname,
  } = row;
  const dataa = new Date(data);
  console.log("asssss", dataa.getMonth());
  return (
    <>
      <tr>
        <td>{id}</td>
        <td>{salesNumber}</td>
        <td>{data}</td>
        <td>{country?.name}</td>
        <td>{productname?.name}</td>
        <td>{slaughteredCattle}</td>
        <td>{amount}</td>
        <td>{loss}</td>
        <td>{price}</td>
        <td>{purchase}</td>
        <td>{nds}</td>
        <td>{veterinar}</td>
        <td>{goStandart}</td>
        <td>{gigiena}</td>
        <td>{customer}</td>
        <td>{deklarent}</td>
        <td>{delivery}</td>
        <td>{swift}</td>
        <td>{ministryFinance}</td>
        <td>{certificate}</td>
        <td>{sPOT}</td>
        <td>{sES}</td>
        <td>{thatsall}</td>
        <td>{accountnumber?.name}</td>
        <td>{furaCondition?.name}</td>
        <td>{finisheddate}</td>
        <td>{willBePaid}</td>
        <td>{residue}</td>
        <td>{finishedmonth}</td>
        <td>{buymonth}</td>
        <td>{comment}</td>
      </tr>
    </>
  );
};

export default Row;
