import React, { useState } from "react";
import Chart from "react-apexcharts";
import { Card, CardHeader, CardBody, Row } from "reactstrap";
import Data from "../variables/data";

const createOption = (x, y) => ({
  x,
  y,
});

const AriaChart = () => {
  const datas = Data();
  const defaultOption = datas.map((e) =>
    createOption(e.tfsana, e.tfsum.replaceAll(",", ""))
  );

  const data = {
    series: [
      {
        name: "Миқдор",
        data: defaultOption,
      },
    ],
    options: {
      chart: {
        type: "area",
        height: 350,
      },
      dataLabels: {
        enabled: false,
      },
      stroke: {
        curve: "straight",
      },

      title: {
        text: "",
        align: "left",
        style: {
          fontSize: "14px",
        },
      },
      xaxis: {
        type: "date",
        title: {
          text: "Сана",
        },
        axisBorder: {
          show: false,
        },
        axisTicks: {
          show: false,
        },
      },
      yaxis: {
        tickAmount: 4,
        floating: false,
        title: {
          text: "Миқдор",
        },
        labels: {
          style: {
            colors: "#8e8da4",
          },
          offsetY: -7,
          offsetX: 0,
        },
        axisBorder: {
          show: false,
        },
        axisTicks: {
          show: false,
        },
      },
      fill: {
        opacity: 0.5,
      },
      tooltip: {
        x: {
          format: "yyyy",
        },
        fixed: {
          enabled: false,
          position: "topRight",
        },
      },
      grid: {
        yaxis: {
          lines: {
            offsetX: -30,
          },
        },
        padding: {
          left: 20,
        },
      },
    },
  };

  return (
    <>
      <Card className="shadow">
        <CardHeader className="bg-transparent">
          <Row className="align-items-center">
            <div className="col">
              <h6 className="text-uppercase text-gray ls-1 mb-1">
                Хақиқий камомат
              </h6>
            </div>
          </Row>
        </CardHeader>
        <CardBody style={{ overflowX: "scroll" }}>
          {/* Chart */}
          <div className="chart" style={{ minWidth: 500, width: "100%" }}>
            <Chart
              options={data.options}
              series={data.series}
              type="area"
              height={350}
            />
          </div>
        </CardBody>
      </Card>
    </>
  );
};

export default AriaChart;
